
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

export default {
  components: {
    Actionsheet,
    Group,
    XSwitch,
    Selector
  },
  data () {
    return {
      show1: false,
      menus1: {
        menu1: 'Share to friends',
        menu2: 'Share to timeline'
      },
      current:'Share to friends'
    }
  },
  methods: {
    showActionsheet () {
      this.show1=true;
    },
    click(key){
        console.log(key);
        console.log(this.menus1);
        for(let i in this.menus1){
          if(i===key){        //item Json'name'
            let jValue=this.menus1[i];//keyvalue
            this.current=jValue;
          }
        }
    },
  },
}



